@use 'base/variables' as v;
@use 'base/mixins' as m;

.vista-producto {
    gap: 1rem;
    display: grid;
    margin: auto;

    @include m.tablet {
        grid-template-columns: repeat(2, 1fr);
    }

    .imagen {
        text-align: center;
    }

    .imagen-producto img {
        object-fit: contain;
        aspect-ratio: 1;
        width: 100%;
        background-color: darken(v.$piel, 10%);
    }

    h1 {
        margin: 0 0 2rem 0;
        font-size: 2.5rem;
    }

    h2 {
        text-align: left;
        font-size: 1.3rem;
    }

    p {
        margin: .5rem 0 1rem 0;
        text-align: justify;
        font-size: 1.2rem;
    }

    .descripcion {
        display: grid;
        gap: 2rem;
    }
}

