@import url("https://fonts.googleapis.com/css2?family=Caveat:wght@400;700&family=Dancing+Script:wght@400;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400;700&family=Indie+Flower&family=Kalam:wght@300;400;700&display=swap");
.boton {
  background-color: rgb(240, 240, 240);
  border-radius: 1rem;
  border: 1px solid rgb(84, 84, 84);
  font-size: 1.2rem;
  margin-top: 0;
  padding: 1rem;
  width: 100%;
  text-transform: uppercase;
}
.boton:hover {
  background-color: #e3e3e3;
  cursor: pointer;
}

.boton-contacto {
  background-color: rgb(240, 240, 240);
  border-radius: 1rem;
  border: 1px solid rgb(84, 84, 84);
  font-size: 1.2rem;
  margin-top: 0.5rem;
  padding: 1rem;
  width: 100%;
  text-transform: uppercase;
}
.boton-contacto:hover {
  background-color: #e3e3e3;
  cursor: pointer;
}
@media (min-width: 768px) {
  .boton-contacto {
    background-color: rgb(240, 240, 240);
    border-radius: 1rem;
    border: 1px solid rgb(84, 84, 84);
    font-size: 1rem;
    margin-top: 0.5rem;
    padding: 0.5rem;
    width: 10rem;
    text-transform: uppercase;
  }
  .boton-contacto:hover {
    background-color: #e3e3e3;
    cursor: pointer;
  }
}

.contenedor-boton {
  text-align: end;
}

html {
  font-size: 16px;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  background-color: rgb(246, 240, 223);
  font-family: "Kalam", cursive;
  font-weight: 300;
}

h1 {
  font-family: "Dancing Script", cursive;
  font-size: 2.5rem;
  padding: 0 0.5rem;
  text-align: center;
}

h2 {
  margin: 0;
  font-weight: 400;
  text-align: center;
}

a {
  text-decoration: none;
}

p {
  font-size: 1.4rem;
}

