.vista-producto {
  gap: 1rem;
  display: grid;
  margin: auto;
}
@media (min-width: 768px) {
  .vista-producto {
    grid-template-columns: repeat(2, 1fr);
  }
}
.vista-producto .imagen {
  text-align: center;
}
.vista-producto .imagen-producto img {
  object-fit: contain;
  aspect-ratio: 1;
  width: 100%;
  background-color: #ebddb7;
}
.vista-producto h1 {
  margin: 0 0 2rem 0;
  font-size: 2.5rem;
}
.vista-producto h2 {
  text-align: left;
  font-size: 1.3rem;
}
.vista-producto p {
  margin: 0.5rem 0 1rem 0;
  text-align: justify;
  font-size: 1.2rem;
}
.vista-producto .descripcion {
  display: grid;
  gap: 2rem;
}

.contenedor-nosotros {
  max-width: 60rem;
  margin: auto;
  padding: 1rem;
}
.contenedor-nosotros .fotos {
  border-style: dotted;
  border-width: 0.3rem 0;
  gap: 3rem;
  display: grid;
  padding: 1rem 0;
}
@media (min-width: 768px) {
  .contenedor-nosotros .fotos {
    grid-template-columns: repeat(3, 1fr);
  }
}
.contenedor-nosotros .fotos div {
  align-items: center;
  display: flex;
  justify-content: center;
}
.contenedor-nosotros .fotos img {
  width: 80%;
}
@media (min-width: 768px) {
  .contenedor-nosotros .fotos img {
    width: 100%;
  }
}
.contenedor-nosotros .descripcion {
  display: grid;
  gap: 2rem;
  margin-top: 3rem;
}
@media (min-width: 768px) {
  .contenedor-nosotros .descripcion {
    grid-template-columns: 1fr 3fr;
  }
}
.contenedor-nosotros .descripcion p {
  font-size: 1.1rem;
  margin-top: 0;
  text-align: justify;
}

.contenedor-contacto {
  max-width: 60rem;
  margin: auto;
  padding: 1rem;
}

.contacto {
  border-style: dotted;
  border-width: 0.3rem 0;
  display: grid;
  gap: 3rem;
  padding: 2rem 0;
}
@media (min-width: 768px) {
  .contacto {
    grid-template-columns: repeat(2, 1fr);
  }
}
.contacto .informacion-contacto {
  margin: 0 auto;
}
.contacto h2 {
  font-size: 1.4rem;
  font-weight: 300;
  text-align: left;
  margin-bottom: 1rem;
}
.contacto p {
  margin: 1rem;
  font-size: 1.2rem;
}

.politicas {
  display: grid;
  gap: 3rem;
  max-width: 60rem;
  margin: auto;
  padding: 1rem;
}
@media (min-width: 768px) {
  .politicas {
    grid-template-columns: repeat(2, 1fr);
  }
}
.politicas h2 {
  font-size: 1.2rem;
  text-transform: uppercase;
}
.politicas p {
  font-size: 1rem;
  text-align: justify;
}

.enviado {
  text-align: center;
  text-transform: uppercase;
  color: #00b341;
}

.header-logo {
  display: flex;
  height: 10rem;
  width: 100%;
  justify-content: center;
  margin-top: 1rem;
}
.header-logo img {
  height: 100%;
  width: 100%;
}

@media (min-width: 768px) {
  .contenedor-navegacion {
    background-color: rgba(248, 215, 232, 0.5);
  }
}

.navegacion {
  max-width: 100rem;
  margin: 1rem auto 0 auto;
}
@media (min-width: 768px) {
  .navegacion {
    padding: 0.5rem 0;
  }
}
.navegacion nav {
  display: block;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .navegacion nav {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
  }
}
.navegacion a {
  background-color: rgba(248, 215, 232, 0.5);
  border: 1px solid rgb(238, 130, 238);
  display: block;
  color: rgb(84, 84, 84);
  font-family: "Dancing Script", cursive;
  font-size: 1.8rem;
  margin: 0 0.5rem 1rem 0.5rem;
  padding: 0.5rem;
  text-align: center;
  transition: color 0.3s ease;
}
@media (min-width: 768px) {
  .navegacion a {
    padding: 0;
  }
}
.navegacion a:hover {
  color: rgb(238, 130, 238);
  transition: color 0.3s ease;
}
@media (min-width: 768px) {
  .navegacion a {
    background-color: initial;
    border: none;
    margin: 0;
  }
}
.navegacion .selec {
  color: rgb(238, 130, 238);
}

.footer {
  background-color: rgba(248, 215, 232, 0.5);
}
.footer p {
  font-size: 1.2rem;
}
.footer .footer-contenido {
  align-items: center;
  display: flex;
  flex-flow: wrap;
  justify-content: center;
  padding: 0 2rem;
  max-width: 100rem;
  margin: auto;
  column-gap: 2rem;
}
@media (min-width: 768px) {
  .footer .footer-contenido {
    justify-content: space-between;
  }
}

.footer-contacto, .footer-redes_sociales {
  align-items: center;
  display: flex;
  column-gap: 1rem;
}

.footer-contacto {
  justify-content: center;
  flex-direction: column;
}
.footer-contacto p {
  margin: 0.5rem;
}
@media (min-width: 768px) {
  .footer-contacto {
    flex-direction: row;
  }
}

.contacto-whatsapp, .contacto-mail {
  align-items: center;
  display: flex;
}

.footer-redes_sociales img {
  border-radius: 50%;
  width: 2.3rem;
  height: 2.3rem;
}

.envio {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1rem;
  gap: 3rem;
  text-align: center;
}
.envio img, .envio svg {
  width: 4rem;
}
@media (min-width: 768px) {
  .envio img, .envio svg {
    width: 10rem;
  }
}

.tienda-peluches > h2 {
  background-color: rgba(248, 215, 232, 0.5);
  font-size: 2rem;
  padding: 1rem 0 0.5rem 0;
  text-transform: uppercase;
  display: block;
}

.contenido-tienda {
  padding: 1rem;
  max-width: 100rem;
  margin: auto;
}
@media (min-width: 768px) {
  .contenido-tienda {
    display: grid;
    grid-template-columns: 4fr 1fr;
    gap: 2rem;
  }
}
.contenido-tienda img {
  aspect-ratio: 1;
  object-fit: cover;
  width: 100%;
}

.tienda {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(2, 1fr);
}
@media (min-width: 768px) {
  .tienda {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 1024px) {
  .tienda {
    grid-template-columns: repeat(4, 1fr);
  }
}
.tienda .producto {
  background-color: rgba(248, 215, 232, 0.5);
}
.tienda .producto:hover {
  -webkit-box-shadow: 3px 6px 10px 10px rgba(0, 0, 0, 0.15);
  -moz-box-shadow: 3px 6px 10px 10px rgba(0, 0, 0, 0.15);
  box-shadow: 3px 6px 10px 10px rgba(0, 0, 0, 0.15);
}
.tienda img {
  padding: 0.5rem;
}
.tienda p {
  text-align: center;
  margin: 0 0 0.5rem 0;
}
.tienda a {
  color: #000000;
}
.tienda .precio {
  color: rgb(84, 84, 84);
}

.otros {
  background-color: rgba(248, 215, 232, 0.5);
  justify-content: center;
  margin-top: 2rem;
  margin: 2rem auto 0 auto;
  padding: 1rem 0 0.5rem 0;
  width: 70%;
}
@media (min-width: 768px) {
  .otros {
    margin-top: 0;
    width: initial;
  }
}
.otros .contenedor-imagen {
  margin: 1rem;
}
.otros img:hover {
  opacity: 0.5;
}
.otros h2 {
  text-transform: uppercase;
}
.otros .nuevos {
  padding-top: 1rem;
}

.formulario .campo {
  display: flex;
  gap: 1rem;
  margin: 0 0 0.7rem 0;
}
.formulario .campo-label {
  flex: 0 0 4rem;
  margin: auto 0;
  text-align: end;
}
.formulario .campo-field {
  padding: 0.8rem;
  width: 100%;
  border-radius: 0.5rem;
  border: 1px solid rgb(84, 84, 84);
  font-size: 1.2rem;
}
@media (min-width: 768px) {
  .formulario .campo-field {
    font-size: 1rem;
    padding: 0.3rem;
  }
}
.formulario .campo.success input, .formulario .campo.campo.success textarea {
  border-color: #00b341;
}
.formulario .campo.error input, .formulario .campo.error textarea {
  border-color: #ff2825;
}
.formulario .campo .error {
  color: #ff2825;
  font-size: 1rem;
  display: block;
}
.formulario .mensaje {
  margin-top: 0.3rem;
}
.formulario .campo-mensaje {
  resize: none;
}

.d-none {
  display: none;
}

.galeria ul {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin: 0;
  padding: 0;
}
.galeria img {
  height: 100%;
  height: 8rem;
}
.galeria img:hover {
  cursor: pointer;
}

.contenedor-hero {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.slider {
  width: 100%;
  max-width: 100rem;
  height: 40rem;
  border-radius: 1rem;
  overflow: hidden;
}

.slides {
  width: 400%;
  height: 40rem;
  display: flex;
}

.slides input {
  display: none;
}

.slide {
  width: 25%;
  transition: 2s;
}

.slide img {
  width: 100%;
  max-width: 100rem;
  height: 40rem;
  object-fit: cover;
}

.navegation-manual {
  position: absolute;
  width: 15rem;
  max-width: 100rem;
  margin: -4rem 0 0 calc(min(50rem, 50%) - 7.5rem);
  display: flex;
  justify-content: center;
  text-align: center;
  padding: 1rem;
  background-color: rgba(128, 128, 128, 0.3);
  border-radius: 5rem;
}

.manual-btn {
  border: 2px solid #FFFFFF;
  padding: 0.5rem;
  border-radius: 1rem;
  cursor: pointer;
  transition: 1s;
}

.manual-btn:not(:last-child) {
  margin-right: 4rem;
}

.manual-btn:hover {
  background: #FFFFFF;
}

#radio1:checked ~ .first {
  margin-left: 0;
}

#radio2:checked ~ .first {
  margin-left: -25%;
}

#radio3:checked ~ .first {
  margin-left: -50%;
}

.navegation-auto {
  position: absolute;
  display: flex;
  width: 15rem;
  max-width: 100rem;
  justify-content: center;
  margin: 37rem 0 0 calc(min(50rem, 50%) - 7.5rem);
}

.navegation-auto div {
  border: 2px solid #FFFFFF;
  padding: 0.5rem;
  border-radius: 1rem;
  transition: 1s;
}

.navegation-auto div:not(:last-child) {
  margin-right: 4rem;
}

#radio1:checked ~ .navegation-auto .auto-btn1 {
  background: #FFFFFF;
}

#radio2:checked ~ .navegation-auto .auto-btn2 {
  background: #FFFFFF;
}

#radio3:checked ~ .navegation-auto .auto-btn3 {
  background: #FFFFFF;
}