@use 'base/mixins' as m;

.contenedor-nosotros {
    max-width: 60rem;
    margin: auto;
    padding: 1rem;

    .fotos {
        border-style: dotted;
        border-width: .3rem 0;
        gap: 3rem;
        display: grid;
        padding: 1rem 0;

        @include m.tablet {
            grid-template-columns: repeat(3, 1fr);
        }

        div {
            align-items: center;
            display: flex;
            justify-content: center;
        }
        
        img {
            width: 80%;
            
            @include m.tablet {
                width: 100%;
            }
        }
    }

    .descripcion {
        display: grid;
        gap: 2rem;
        margin-top: 3rem;

        @include m.tablet {
            grid-template-columns: 1fr 3fr;
        }

        p {
            font-size: 1.1rem;
            margin-top: 0;
            text-align: justify;
        }
    }
}