@use 'base/mixins' as m;
@use 'base/variables' as v;

.galeria {

    ul {
        display: flex;
        justify-content: center;
        gap: 1rem;
        margin: 0;
        padding: 0;
    }

    img {
        height: 100%;
        height: 8rem;

        &:hover {
            cursor: pointer;
        }
    }
}

