@use 'base/variables' as v;

html {
    font-size: 16px;
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}

body {
    background-color: v.$piel;
    font-family: v.$fuente-cuarta;
    font-weight: 300;
}

h1 {
    font-family: v.$fuente_principal;
    font-size: 2.5rem;
    padding: 0 .5rem;
    text-align: center;
}

h2 {
    margin: 0;
    font-weight: 400;
    text-align: center;
}

a {
    text-decoration: none;
}

p {
    font-size: 1.4rem;
}