@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenedor-contacto {
    max-width: 60rem;
    margin: auto;
    padding: 1rem;
}

.contacto {
    border-style: dotted;
    border-width: .3rem 0;
    display: grid;
    gap: 3rem;
    padding: 2rem 0;

    @include m.tablet {
        grid-template-columns: repeat(2, 1fr);
    }

    .informacion-contacto {
        margin: 0 auto;
    }

    h2 {
        font-size: 1.4rem;
        font-weight: 300;
        text-align: left;
        margin-bottom: 1rem;
    }

    p {
        margin: 1rem;
        font-size: 1.2rem;
    }
}

.politicas {
    display: grid;
    gap: 3rem;
    max-width: 60rem;
    margin: auto;
    padding: 1rem;

    @include m.tablet {
        grid-template-columns: repeat(2, 1fr);
    }

    h2 {
        font-size: 1.2rem;
        text-transform: uppercase;
    }

    p {
        font-size: 1rem;
        text-align: justify;
    }
}

