@use 'base/variables' as v;

@mixin telefono {
    @media (min-width: v.$telefono) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}

@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}

@mixin boton($font-size: 1.2rem, $padding: 1rem, $margin-top: 0, $width: 100%) {
    background-color: v.$plomo-claro;
    border-radius: 1rem;
    border: 1px solid v.$plomo;
    font-size: $font-size;
    margin-top: $margin-top;
    padding: $padding;
    width: $width;
    text-transform: uppercase;

    &:hover {
        background-color: darken(v.$plomo-claro, 5);
        cursor: pointer;
    }
}
