@use 'base/variables' as v;
@use 'base/mixins' as m;

.tienda-peluches > h2 {
    background-color: v.$rosado;
    font-size: 2rem;
    padding: 1rem 0 .5rem 0;
    text-transform: uppercase;
    display: block;
}

.contenido-tienda {
    padding: 1rem;
    max-width: 100rem;
    margin: auto;

    @include m.tablet {
        display: grid;
        grid-template-columns: 4fr 1fr;
        gap: 2rem;
    }

    img {
        aspect-ratio: 1;
        object-fit: cover;
        width: 100%;
    }
}

.tienda {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);

    @include m.tablet {
        grid-template-columns: repeat(3, 1fr);
    }

    @include m.desktop {
        grid-template-columns: repeat(4, 1fr);
    }

    .producto {
        background-color: v.$rosado;

        &:hover {
            -webkit-box-shadow: 3px 6px 10px 10px rgba(0,0,0,0.15);
            -moz-box-shadow: 3px 6px 10px 10px rgba(0,0,0,0.15);
            box-shadow: 3px 6px 10px 10px rgba(0,0,0,0.15);
        }
    }

    img {
        padding: .5rem;
    }

    p {
        text-align: center;
        margin: 0 0 .5rem 0;
    }

    a {
        color: v.$negro;
    }

    .precio {
        color: v.$plomo;
    }
}

.otros {
    background-color: v.$rosado;
    justify-content: center;
    margin-top: 2rem;
    margin: 2rem auto 0 auto;
    padding: 1rem 0 .5rem 0;
    width: 70%;

    @include m.tablet {
        margin-top: 0;
        width: initial;
    }

    .contenedor-imagen {
        margin: 1rem;
    }

    img {

        &:hover {
            opacity: .5;
        } 
    }

    h2 {
        text-transform: uppercase;
    }

    .nuevos {
        padding-top: 1rem;
    }
}