@use 'base/variables' as v;
@use 'base/mixins' as m;

.formulario {

    .campo {
        display: flex;
        gap: 1rem;
        margin: 0 0 .7rem 0;
    }

    .campo-label {
        flex: 0 0 4rem;
        margin: auto 0;
        text-align: end;
    }

    .campo-field {
        padding: .8rem;
        width: 100%;
        border-radius: .5rem;
        border: 1px solid v.$plomo;
        font-size: 1.2rem;

        @include m.tablet {
            font-size: 1rem;
            padding: .3rem;
        }
    }

    .campo.success input, .campo.campo.success textarea {
        border-color: v.$verde;
    }

    .campo.error input, .campo.error textarea {
        border-color: v.$rojo;
    }

    .campo .error {
        color: v.$rojo;
        font-size: 1rem;
        display: block;
    }

    .mensaje {
        margin-top: .3rem;
    }

    .campo-mensaje {
        resize: none;
    }
}

.d-none {
    display: none;
}