@use 'base/variables' as v;
@use 'base/mixins' as m;

.header-logo {
    display: flex;
    height: 10rem;
    width: 100%;
    justify-content: center;
    margin-top: 1rem;

    img {
        height: 100%;
        width: 100%;
    }
}

.contenedor-navegacion {
    
    @include m.tablet {
        background-color: v.$rosado;
    }
}

.navegacion {
    max-width: 100rem;
    margin: 1rem auto 0 auto;

    @include m.tablet {
        padding: .5rem 0;
    }

    nav {
        display: block;
        flex-direction: column;
        align-items: center;

        @include m.tablet {
            display: flex;
            flex-direction: row;
            justify-content: space-around;
        }
    }

    a {
        background-color: v.$rosado;
        border: 1px solid v.$violeta;
        display: block;
        color: v.$plomo;
        font-family: v.$fuente_principal;
        font-size: 1.8rem;
        margin: 0 .5rem 1rem .5rem;
        padding: .5rem;
        text-align: center;
        transition: color .3s ease;

        @include m.tablet {
            padding: 0;
        }

        &:hover {
            color: v.$violeta;
            transition: color .3s ease;
        }

        @include m.tablet {
            background-color: initial;
            border: none;
            margin: 0;
        }
    }

    .selec {
        color: v.$violeta;
    }
}
