@use 'base/mixins' as m;
@use 'base/variables' as v;

.contenedor-hero {
    margin: 0;
    padding: 0;
    //height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.slider {
    width: 100%;
    max-width: 100rem;
    height: 40rem;
    border-radius: 1rem;
    overflow: hidden;
}

.slides {
    width: 400%;
    height: 40rem;
    display: flex;
}

.slides input {
    display: none;
}

.slide {
    width: 25%;
    transition: 2s;
}

.slide img {
    width: 100%;
    max-width: 100rem;
    height: 40rem;
    object-fit: cover;
}

// css para navegacion manual

.navegation-manual {
    position: absolute;
    width: 15rem;
    max-width: 100rem;
    margin: -4rem 0 0 calc(min(50rem, 50%) - 15rem/2);
    display: flex;
    justify-content: center;
    text-align: center;

    padding: 1rem;
    background-color: rgba(128, 128, 128, .3);
    border-radius: 5rem;
}

.manual-btn {
    border: 2px solid v.$blanco;
    padding: .5rem;
    border-radius: 1rem;
    cursor: pointer;
    transition: 1s;
}

.manual-btn:not(:last-child) {
    margin-right: 4rem;
}

.manual-btn:hover {
    background: v.$blanco;
}

#radio1:checked ~ .first {
    margin-left: 0;
}

#radio2:checked ~ .first {
    margin-left: -25%;
}

#radio3:checked ~ .first {
    margin-left: -50%;
}

// css para navegacion automatica

.navegation-auto {
    position: absolute;
    display: flex;
    width: 15rem;
    max-width: 100rem;
    justify-content: center;
    margin: 37rem 0 0 calc(min(50rem, 50%) - 15rem/2);
}

.navegation-auto div {
    border: 2px solid v.$blanco;
    padding: .5rem;
    border-radius: 1rem;
    transition: 1s;
}

.navegation-auto div:not(:last-child) {
    margin-right: 4rem;
}

#radio1:checked ~ .navegation-auto .auto-btn1 {
    background: v.$blanco;
}

#radio2:checked ~ .navegation-auto .auto-btn2 {
    background: v.$blanco;
}

#radio3:checked ~ .navegation-auto .auto-btn3 {
    background: v.$blanco;
}
