@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer {
    background-color: v.$rosado;

    p {
        font-size: 1.2rem;
    }

    .footer-contenido {
        align-items: center;
        display: flex;
        flex-flow: wrap;
        justify-content: center;
        padding: 0 2rem;
        max-width: 100rem;
        margin: auto;
        column-gap: 2rem;

        @include m.tablet {
            justify-content: space-between;
        }
    }
}

.footer-contacto, .footer-redes_sociales {
    align-items: center;
    display: flex;
    column-gap: 1rem;
}

.footer-contacto {
    justify-content: center;
    flex-direction: column;

    p {
        margin: .5rem;
    }

    @include m.tablet {
        flex-direction: row;
    }
}


.contacto-whatsapp, .contacto-mail {
    align-items: center;
    display: flex;
}

.footer-redes_sociales {

    img {
        border-radius: 50%;
        width: 2.3rem;
        height: 2.3rem;
    }
}


